.Burger {
  width: 100%;
  margin: auto;
  height: 250px;
  max-height: 200px;
  overflow-y: auto;
  text-align: center;
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 20px;
}

@media only screen and (min-width: 425px) {
  .Burger {
    max-height: 300px;
    width: 400px;
  }
}

@media only screen and (min-width: 560px) {
  .Burger {
    max-height: 300px;
    width: 400px;
  }
}
